<template>
    <div class="site-map">
        <h2>个人博客's SiteMap</h2>
        <div class="title">
            <router-link to="/index" class="person-blog">
                个人博客
            </router-link>
            >>
            <a href="#">站点地图</a>
        </div>
        <dl class="article-list">
            <dt>最新文章</dt>
            <dd v-for="item in blogList" :key="item.id">
                <router-link :to="`/detail?bid=${item.id}`">{{item.title}}</router-link>
            </dd>
        </dl>
    </div>
</template>


<script>
export default {
    data(){
        return {
            blogList:[]
        }
    },
    mounted(){
        this.$axios.get('/queryAllBlog',{
            params:{}
        }).then(res =>{
            this.blogList = res.data.data;
        }).catch(e => console.log(e))
    }

}
</script>


<style lang="scss">
    @import "@/assets/css/sitemap/site-map.scss"
</style>